import Vue from 'vue'
// 
// import Modal from './modal/index'

import ModalTemp from './modal/src/main.vue'

function create () {
  const vm = new Vue({
    render (h) {
      return h(ModalTemp)
    }
  }).$mount()

  document.body.appendChild(vm.$el)
}

export default create()

